body {
  background-color: rgba(28, 45, 121, 0.918);
}

.header {
  background-color: rgb(27, 27, 27);
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  box-sizing: border-box;
}

.menu {
  display: flex;
  list-style: none;
  cursor: pointer;

}

.logo {
  color: whitesmoke;
  font-weight: bold;
  text-decoration: none;
}

.menu li {
  margin-left: 40px;
  color: whitesmoke;
}

.menuNav {
  color: whitesmoke;
  font-weight: bold;
  text-decoration: none;
}

.menuNav:hover {
  color: rgb(62, 86, 226);

}

h1 {
  color: white;
  text-align: center;
}

h2 {
  color: white;
  text-align: center;
  font-weight: 300;
}

h3 {
  color: white;
}

h4 {
  color: white;
  text-align: center;
}

.box {
  width: 15vw;
  height: 30vh;
  background-color: rgba(28, 45, 121, 0.918);
  border: 3px rgb(27, 27, 27) solid;
  margin: 10px;
}

.box:hover {
  border: 3px white solid;
  cursor: pointer;
}

#productPainel {
  display: flex;
  justify-content: center;
  align-items: center;

}

.container {
  display: flex;
  margin: auto;
  margin-top: 2%;
  width: 95vw;
  height: 79vh;
  background-color: rgb(174, 174, 177);
  box-shadow: 2px 2px 20px 5px rgba(0, 0, 0, 0.2);

}

.input {
  width: 5vw;
  display: flex;
  align-items: flex-end;
}

.listInputs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 0;
  padding-left: 5%;
  width: 100%;

}

label {
  color: white;
}

.ingredientes {
  display: inline-block;
  width: 20vw;
  max-height: 79vh;
  background-color: rgb(94, 99, 99);
  box-sizing: border-box;
  overflow-y: auto;
  overflow-x: hidden;
}

#boxPainels {
  display: inline;
}

.ingredientesBox {
  display: inline-block;
  background-color: rgba(175, 66, 39, 0.925);
  width: 11.7%;
  height: 55px;
  border: 1px black solid;
  margin: 0.3%;
  overflow: auto;
}

.Produtosboxes {
  display: inline-block;
  background-color: rgba(38, 128, 72, 0.925);
  width: 10%;
  height: 191px;
  border: 1px black solid;
  margin: 0.2%;
  overflow: auto;
}

.deleteProduto {
  text-align: center;
  color: white;
  cursor: pointer;
  font-weight: bold;
  background-color: darkslateblue;
  width: 100%;
  margin-top: 1%;
}

.deleteProduto:hover {
  color: brown;
}

.textBox {
  padding: 0;
  font-size: 15px;
  text-align: center;
  margin: 0;

}

.btn_delete_item {
  background: none;
  border: none;
  text-align: center;
  color: white;
  font-weight: bold;
  background-color: black;
}

.btn_delete_item:hover {
  color: brown;
  cursor: pointer;
}

#boxes {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.caixa {
  background-color: plum;
}

#painelSoma {
  display: flex;
  width: 100%;
  align-items: center;
  background-color: rgba(18, 18, 19, 0.836);
}

.valores {
  width: 50%;
}

#nome_produto_cadastro {
  height: 30%;
  margin: 1%;
}

.btn_total_painel {
  width: 15%;
  height: 4vh;
  margin-left: 1%;
  margin-right: 1%;
  background-color: rgba(175, 66, 39, 0.925);
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  border: 1px rgba(18, 18, 19, 0.836) solid;

}

.btn_total_painel:hover {
  border: 1px white solid;
  box-shadow: 2px 2px 5px 2px rgba(255, 255, 255, 0.2);
  ;
}

.input_total_painel {

  width: 6%;
  height: 3vh;
}

.guide {
  display: block;
  margin: auto;
  padding-top: 1%;
  width: 100%;
  justify-content: center;
}

.steps {
  width: 50%;
  margin: auto;
  height: 20%;
  margin-top: 2%;
}

.step {
  margin-bottom: 1%;
  box-shadow: 2px 2px 20px 5px rgba(0, 0, 0, 0.2);
  padding: 1%;
  background-color: rgb(27, 27, 27);
}

.dataItens {
  background-color: blue;
  border: 3px black solid;
}

.salvarProduto {
  width: 34%;
  height: 30px;
  margin-top: 15px;

  margin-left: 17px;
}

.selectFood {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2%;
  color: white;
  background-color: brown;
  margin-bottom: 1%;
}

.inputFoodList {
  width: 40px;
}

.loginBox {

  margin-left: 40vw;
  margin-top: 25vh;
  background-color: rgb(133, 10, 10);
  box-shadow: 1px 1px 5px black;
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1%;

}

.inputLogin {
  width: 40%;
  box-shadow: 1px 1px 4px black;
  margin: 1%;
}

.btn_login {
  margin: 1%;
  margin-bottom: 5%;
  padding: 1%;
  width: 42%;
  box-shadow: 1px 1px 4px black;
}

@media (max-width:600px) {
  .header {
    flex-direction: column;
    padding: 0px;
    justify-content: center;
  }

  .logo {
    font-size: 18px;
  }

  .menu li {
    font-size: 12px;
    margin-right: 2px;
  }

  .menu {
    margin-left: -40px;
  }

  .loginBox {
    width: 80%;
    margin-left: 9vw;
  }

  .container {
    flex-direction: column;
  }

  .ingredientes {
    width: 100%;
    padding-bottom: 15px;
  }

  .input {
    width: 50vw;
  }

  #painelSoma {
    flex-direction: column;
  }

  .input_total_painel {
    width: 30%;
  }

  .btn_total_painel {
    width: 50%;
    height: 6vh;
    margin: 2%;
  }

  .valores {
    width: 100%;
    font-size: 18px;
    margin: 5%;

  }

  .ingredientesBox {
    width: 30vw;
    height: 10vh;
  }

  .textBox {
    font-size: 12px;
  }

  .btn_delete_item {
    width: 100%;
  }
}